import React from 'react'
import styled, {css} from 'styled-components'

const StyledButton = styled.button`
    --clr: ${
        props => props.error? "rgb(194, 15, 47)": 
        props => props.warning ? "rgb(237, 139, 1)" : "rgb(23, 63, 116)"};
    color: white;
    padding: 4px 16px;
    margin: 8px;
    font-size: 1rem;
    border: 2px solid var(--clr);
    background-color: var(--clr);
    border-radius: 5px;
    transition: all 0.15s linear;
    display: ${props => props.fullWidth ? "block" : "inline-block"};
    
    ${props => props.outline && css`
        border: 2px solid var(--clr);
        background-color: white;
        color: var(--clr);
    `}
    
    &:hover { 
        opacity: 0.75 
    }
    &:focus { 
        outline: none 
    }
    &:disabled { 
        color: rgb(175,175,175); background-color: rgb(225,225,225); 
        border: ${props => props.outline ? "2px solid rgb(175,175,175)" : "2px solid rgb(225,225,225)"}
    }
    &:active {
        color: ${props => props.outline ? "white" : "var(--clr)"}; 
        background-color: ${props => props.outline ? "var(--clr)": "white"}
    }
`;

const Button = (props) => {
    return (
        <StyledButton 
            disabled={props.disabled} 
            error={props.error} 
            warning={props.warning}
            outline={props.outline}
            onClick={props.onClick}
            fullWidth = {props.fullWidth}
        >
            {props.children}
        </StyledButton>
    )
}

export default Button
